import React,{useState} from "react";
import logo from './logo.png'
import info from './letter-i.png'
import AboutImage from './letter-i.png'
import closeIcon from './cancel.png'

const Navbar = () => {
  let isArrayFunction = (inputArray) => {
    if (Array.isArray(inputArray) && inputArray.length !== 0) {
      return false;
    } else {
      return true;
    }
  };
  const [isModelOpen, setIsModelOpen] = useState(false);
  const toggleModel = () => {
      setIsModelOpen(!isModelOpen)
      if(!isModelOpen){
        document.body.style.overflow = 'hidden';
      }
      else{
        document.body.style.overflow = 'unset';
      }
  }
  return (

    <div id="Navbar">
      <div className="about" style={isModelOpen?{display:"flex"}:{display:"none"}}>
            <div className="about-card">
                <div className="header">
                    <h3>About this project</h3>
                    <img className="close" src={closeIcon} onClick={toggleModel} alt="" />
                </div>
                <div className="body">
                  <p><b>Title:</b> MedAssist ( Your personal health assistant )</p>
                  <p><b>Problem Solved:</b> Analyses symptoms and gives probabilty of diseases</p>
                  <p><b>Important Links:</b> <a href="#">Abstract</a> | <a href="#">PPT</a> | <a href="#">Document</a></p>
                  
                  <b>Made by:</b> F.E. Div D DIT, Pimpri
                  <ul>
                    <li>Vikas Saini</li>
                    <li>Amit Shelke</li>
                    <li>Kashif shaikh</li>
                    <li>Shanidev Kashyap</li>
                    <li>Aashif shaikh</li>
                    <li>Daniyal</li>
                  </ul>
                </div>
                {/* <div className="footer"></div> */}
            </div>
        </div>
      <header class="usa-header usa-header--basic usa-header--megamenu padding-bottom-4 padding-top-6">
        <div class="usa-nav-container">
          <div class="usa-navbar">
            <div class="usa-logo margin-0" id="basic-mega-logo">
              <em class="usa-logo__text">
                <div className="branding">
                  <div className="left">
                    <img style={{height:'70px'}} className="logo" src={logo} alt="" />
                    <div className="name-and-moto">
                      <h3 className="display-inline-block padding-left-1">MedAssist</h3>
                      {/* <p>We make lives better!</p> */}
                      {/* <p style={{color:"rgba(21, 23, 31, 0.579)"}}> Your personal health assistant!</p> */}
                    </div>
                  </div>
                  <div className="right">
                    <img className="about-img" src={AboutImage} onClick={toggleModel} alt="" />
                  </div>
                </div>
              </em>
            </div>
          </div>
        </div>
      </header>
    </div>
  );
};

export default Navbar;